<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫ -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÊùÉÈôêÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ËßíËâ≤ÂàóË°®</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoleDialogVisible = true">Ê∑ªÂä†ËßíËâ≤</el-button>
        </el-col>
      </el-row>
      <el-table :data="rolesList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' : '','vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row :class="[i2 === 0 ? '' : 'bdtop','vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" v-for="item3 in item2.children" :key="item3.id" closable @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="Â∫èÂè∑" type="index"></el-table-column>
        <el-table-column label="ËßíËâ≤ÂêçÁß∞" prop="roleName"></el-table-column>
        <el-table-column label="ËßíËâ≤ÊèèËø∞" prop="roleDesc"></el-table-column>
        <el-table-column label="Êìç‰Ωú" width="350px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="min"
              @click="showEditRoleDialog(scope.row.id)"
            >ÁºñËæë</el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="min"
              @click="removeRoleById(scope.row.id)"
            >Âà†Èô§</el-button>
            <el-button type="warning" icon="el-icon-setting" size="min" @click="showSetRightDialog(scope.row)">ÂàÜÈÖçÊùÉÈôê</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- Ê∑ªÂä†ËßíËâ≤ÂØπËØùÊ°Ü -->
    <el-dialog
      title="Ê∑ªÂä†ËßíËâ≤"
      :visible.sync="addRoleDialogVisible"
      width="50%"
      @close="addRoleDialogClosed"
    >
      <el-form
        :model="addRolesForm"
        :rules="addRolesFormRules"
        ref="addRolesFormRef"
        label-width="100px"
      >
        <el-form-item label="ËßíËâ≤ÂêçÁß∞" prop="roleName">
          <el-input v-model="addRolesForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="ËßíËâ≤ÊèèËø∞" prop="roleDesc">
          <el-input v-model="addRolesForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="addRoles">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
    <!-- ‰øÆÊîπËßíËâ≤ÂØπËØùÊ°Ü -->
    <el-dialog title="‰øÆÊîπËßíËâ≤" :visible.sync="editRoleDialogVisible" width="50%" @close="editRoleDialogClosed">
      <el-form :model="editRolesForm" :rules="editRolesFormRules" ref="editRolesFormRef" label-width="100px">
        <el-form-item label="ËßíËâ≤ID" prop="roleId">
          <el-input v-model="editRolesForm.roleId" disabled=""></el-input>
        </el-form-item>
        <el-form-item label="ËßíËâ≤ÂêçÁß∞" prop="roleName">
          <el-input v-model="editRolesForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="ËßíËâ≤ÊèèËø∞" prop="roleDesc">
          <el-input v-model="editRolesForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRoleDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="editRolesInfo">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
    <!-- ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°Ü -->
    <el-dialog title="ÂàÜÈÖçÊùÉÈôê" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="allotRights">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      addRoleDialogVisible: false,
      addRolesForm: {
        roleName: '',
        roleDesc: ''
      },
      addRolesFormRules: {
        roleName: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ËßíËâ≤Âêç', trigger: 'blur' },
          { min: 3, max: 10, message: 'ÈïøÂ∫¶Âú® 3 Âà∞ 10 ‰∏™Â≠óÁ¨¶', trigger: 'blur' }
        ],
        roleDesc: [
          { required: false, message: 'ËØ∑ËæìÂÖ•ÊèèËø∞', trigger: 'blur' },
          { min: 5, max: 15, message: 'ÈïøÂ∫¶Âú® 5 Âà∞ 15 ‰∏™Â≠óÁ¨¶', trigger: 'blur' }
        ]
      },
      editRoleDialogVisible: false,
      editRolesForm: {},
      editRolesFormRules: {
        roleName: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ËßíËâ≤Âêç', trigger: 'blur' },
          { min: 3, max: 10, message: 'ÈïøÂ∫¶Âú® 3 Âà∞ 10 ‰∏™Â≠óÁ¨¶', trigger: 'blur' }
        ],
        roleDesc: [
          { required: false, message: 'ËØ∑ËæìÂÖ•ÊèèËø∞', trigger: 'blur' },
          { min: 5, max: 15, message: 'ÈïøÂ∫¶Âú® 5 Âà∞ 15 ‰∏™Â≠óÁ¨¶', trigger: 'blur' }
        ]
      },
      setRightDialogVisible: false,
      rightsList: [],
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      defKeys: [],
      // ÂΩìÂâçÂç≥Â∞ÜÂàÜÈÖçÊùÉÈôêÁöÑid
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñËßíËâ≤ÂàóË°®Â§±Ë¥•ÔºÅ')
      }
      this.rolesList = res.data
      console.log(this.rolesList)
    },
    addRoleDialogClosed() {
      this.$refs.addRolesFormRef.resetFields()
    },
    addRoles() {
      this.$refs.addRolesFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.post('roles', this.addRolesForm)
        if (res.meta.status !== 201) {
          return this.$message.error('Ê∑ªÂä†ËßíËâ≤Â§±Ë¥•ÔºÅ')
        }
        this.$message.success('Ê∑ªÂä†ËßíËâ≤ÊàêÂäüÔºÅ')
        this.addRoleDialogVisible = false
        this.getRolesList()
      })
    },
    async showEditRoleDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('Êü•ËØ¢ËßíËâ≤Â§±Ë¥•ÔºÅ')
      }
      this.editRolesForm = res.data
      this.editRoleDialogVisible = true
    },
    editRoleDialogClosed() {
      this.$refs.editRolesFormRef.resetFields()
    },
    editRolesInfo() {
      this.$refs.editRolesFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put('roles/' + this.editRolesForm.roleId, { roleName: this.editRolesForm.roleName, roleDesc: this.editRolesForm.roleDesc })
        if (res.meta.status !== 200) {
          return this.$message.error('‰øÆÊîπËßíËâ≤‰ø°ÊÅØÂ§±Ë¥•ÔºÅ')
        }
        this.editRoleDialogVisible = false
        this.getRolesList()
        this.$message.success('Êõ¥Êñ∞ËßíËâ≤‰ø°ÊÅØÊàêÂäüÔºÅ')
      })
    },
    async removeRoleById(id) {
      // ËØ¢ÈóÆÁî®Êà∑ÊòØÂê¶Âà†Èô§Êï∞ÊçÆ
      const confirmResult = await this.$confirm('Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Êñá‰ª∂, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).catch(err => err)
      // Á°ÆËÆ§Âà†Èô§ÔºåËøîÂõûconfirmÂ≠óÁ¨¶‰∏≤
      // ÂèñÊ∂àÂà†Èô§ÔºåËøîÂõûcancelÂ≠óÁ¨¶‰∏≤
      if (confirmResult !== 'confirm') {
        return this.$message.info('Â∑≤ÂèñÊ∂àÂà†Èô§')
      }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('Âà†Èô§Áî®Êà∑Â§±Ë¥•ÔºÅ')
      }
      this.$message.success('Âà†Èô§Áî®Êà∑ÊàêÂäüÔºÅ')
      this.getRolesList()
    },
    async removeRightById(role, rightId) {
      const confirmResult = await this.$confirm('Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Êñá‰ª∂, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('Â∑≤ÂèñÊ∂àÂà†Èô§')
      }
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('Âà†Èô§ÊùÉÈôêÂ§±Ë¥•ÔºÅ')
      }
      this.$message.success('Âà†Èô§ÊùÉÈôêÊàêÂäüÔºÅ')
      // this.getRolesList()
      // üëÜÊòØ‰∏çÂ•ΩÁöÑ‰ΩìÈ™åÔºåüëáÂçïÁã¨Êõ¥Êñ∞ÊâÄÂú®Â±ÇÁ∫ßÊùÉÈôêÁöÑÊï∞ÊçÆÔºå‰∏çÊõ¥Êñ∞È°µÈù¢
      role.children = res.data
    },
    // Â±ïÁ§∫ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°Ü
    async showSetRightDialog(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÊùÉÈôêÂ§±Ë¥•ÔºÅ')
      }
      // Ëé∑ÂèñÊâÄÊúâÊùÉÈôê‰øùÂ≠òÂà∞Êï∞ÊçÆ
      this.rightsList = res.data
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // ÈÄöËøáÂú∞ËΩ®ÁöÑÂΩ¢ÂºèÔºåËé∑ÂèñËßíËâ≤‰∏ãÊâÄÊúâ‰∏âÁ∫ßÊùÉÈôêÁöÑidÔºåÂπ∂‰øùÂ≠òÂà∞defKeys‰∏≠
    getLeafKeys(node, arr) {
      // Â¶ÇÊûúÂΩìÂâçnode‰∏çÂåÖÂê´childrenÂ±ûÊÄßÔºåÂàôÊòØ‰∏âÁ∫ßËäÇÁÇπ
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    setRightDialogClosed() {
      this.defKeys = []
    },
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('ÂàÜÈÖçÊùÉÈôêÂ§±Ë¥•ÔºÅ')
      }
      this.$message.success('ÂàÜÈÖçÊùÉÈôêÊàêÂäüÔºÅ')
      this.setRightDialogVisible = false
      this.getRolesList()
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
